/* ComplexLayout.css */
.rectangle1 {
  width: 623px;
  height: 425px;
  flex-shrink: 0;
  border-radius: 11px 0px 20px 0px;
  background: #F4F2EE;
  position: absolute;
  bottom: 0;
  right: 0;
}

.rectangle2 {
  width: 623px;
  height: 80px;
  flex-shrink: 0;
  border-radius: 11px 0px 0px 0px;
  background: #Fff;
  box-shadow: 0px 1px 1px 0px rgba(0, 0, 0, 0.25);
  position: absolute;
  bottom: 345px;
  right: 0;
    display: flex;
  align-items: center;
  justify-content: flex-start;
}

.rectangle3{
    width: 114px;
    height: 20px;
    border-radius: 10px;
    background: #8F8080;
    position: absolute;
    bottom: 375px;
    right: 230px;
}
.circle-img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin-right: 11px;  /* Space between images */
}

.rectangle4, .rectangle5, .rectangle6 {
  height: 20px;
  flex-shrink: 0;
  border-radius: 10px;
  background: #C9B7B7;
  position: absolute;
}

.rectangle4 {
  width: 206px;
  border-radius: 10px 0px 0px 10px;
  right: 0;
  bottom: 375px;
}

.rectangle5 {
  width: 555px;
  border-radius: 10px 0px 0px 10px;
  right: 0;
  bottom: 279px;
}

.rectangle6 {
  width: 397px;
  border-radius: 10px;
  right: 157px;
  bottom: 231px;
}

.rectangle7{
    width: 369px;
    height: 196px;
    border-radius: 8px 8px 0px 0px;
    background: #D9D9D9;
    position: absolute;
    bottom: 0;
    right: 186px;
}

.rectangle8{
    width: 155px;
    height: 196px;
    border-radius: 8px 0px 20px 0px;
    background: #D9D9D9;
    position: absolute;
    bottom: 0;
    right: 0;
}